<template>
  <div class="mainPage">
    <el-row>
      <!--   左侧随机图片   -->
      <el-col :span="12" class="article">
        <img src="../../assets/dlam2.jpg" alt="">
      </el-col>

      <!--   右侧星座   -->
      <el-col :span="12" class="constellation">
        <div class="constellationLeft">
          <span style="font-size: 20px">🌸星座运势🌸</span>
          <br>
          <br>
          <span>{{ this.constellationList.data.title }}&nbsp;&nbsp;&nbsp;时间:{{
              this.constellationList.data.time
            }}</span>
          <div style="margin-top: 10px">
            <el-select v-model="constellation" placeholder="请选择你的星座" @change="changeConstellation" clearable>
              <el-option v-for="(item,index) in constellationOption" :key="index" :value="item.constellationTest"
                         :label="item.label"></el-option>
            </el-select>
          </div>
          <div class="luck">
            <ul>
              <li>整体运势:&nbsp;{{ this.constellationList.data.fortune.all }}&nbsp;分</li>
              <li>爱情运势:&nbsp;{{ this.constellationList.data.fortune.love }}&nbsp;分</li>
              <li>事业运势:&nbsp;{{ this.constellationList.data.fortune.work }}&nbsp;分</li>
              <li>财富运势:&nbsp;{{ this.constellationList.data.fortune.money }}&nbsp;分</li>
              <li>健康指数:&nbsp;{{ this.constellationList.data.index.health }}&nbsp;</li>
              <li>商谈指数:&nbsp;{{ this.constellationList.data.index.discuss }}&nbsp;</li>
              <li>幸运色:&nbsp;{{ this.constellationList.data.luckycolor }}&nbsp;</li>
              <li>幸运数字:&nbsp;{{ this.constellationList.data.luckynumber }}&nbsp;</li>
              <li>速配星座:&nbsp;{{ this.constellationList.data.luckyconstellation }}&nbsp;</li>
              <li>短评:&nbsp;{{ this.constellationList.data.shortcomment }}&nbsp;</li>
            </ul>
          </div>
          <div class="luckInfo">
            <div style="margin-bottom: 10px">
              <span><i style="background-color: #80bee4;">整体运势:</i> {{
                  this.constellationList.data.fortunetext.all
                }} </span>
            </div>
            <div>
              <span><i style="background-color: #ea749e;">爱情运势:</i> {{
                  this.constellationList.data.fortunetext.love
                }} </span>
            </div>
            <div>
              <span><i style="background-color: #ab92dc;">事业运势:</i> {{
                  this.constellationList.data.fortunetext.work
                }} </span>
            </div>
            <div>
              <span><i style="background-color: #f39f5b;">财富运势:</i> {{
                  this.constellationList.data.fortunetext.money
                }} </span>
            </div>
            <div>
              <span><i
                  style="background-color: #7fbe58;">健康运势:</i> {{ this.constellationList.data.fortunetext.health }} </span>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  // 组件加载完后触发
  mounted() {
    this.getArticle()
    this.getConstellation()
  },
  components: {},
  data() {
    return {


      // 星座
      constellation: 'taurus',
      constellationList: [],
      constellationOption: [
        {
          constellationTest: 'taurus',
          label: '金牛座'
        },
        {
          constellationTest: 'virgo',
          label: '处女座'
        },
        {
          constellationTest: 'scorpio',
          label: '天蝎座'
        },
        {
          constellationTest: 'pisces',
          label: '双鱼座'
        },
        {
          constellationTest: 'aries',
          label: '白羊座'
        },
        {
          constellationTest: 'gemini',
          label: '双子座'
        },
        {
          constellationTest: 'cancer',
          label: '巨蟹座'
        },
        {
          constellationTest: 'leo',
          label: '狮子座'
        },
        {
          constellationTest: 'libra',
          label: '天秤座'
        },
        {
          constellationTest: 'sagittarius',
          label: '射手座'
        },
        {
          constellationTest: 'capricorn',
          label: '摩羯座'
        },
        {
          constellationTest: 'aquarius',
          label: '水瓶座'
        },
      ],

      // 图片
      imgInfo:'',
      imgDate:[],
    };
  },
  computed: {},
  //方法集合
  methods: {

    getArticle() {
      axios.get('https://api.uomg.com/api/rand.img1?sort=美女&format=json')
          .then((res) => {
            this.imgDate = res.data.imgurl
            console.log('res============>' + JSON.stringify(res.data))
          })
          .catch((error) => {
            console.log(error)
          })
    },



    getConstellation() {
      axios.get('https://api.vvhan.com/api/horoscope?type=' + this.constellation + '&time=today')
          .then((res) => {
            this.constellationList = res.data
            this.$notify({
              title: '成功',
              message: '选择星座成功',
              type: 'success'
            });
          })
          .catch((error) => {
            console.log(error)
          })
    },
    changeConstellation(value) {
      this.constellation = value
      console.log(value)
      this.getConstellation()
    },


  },
}
</script>
<style lang='less' scoped>
.mainPage {
  padding: 0 5px;
  box-sizing: border-box;
  height: 450px;
  overflow: hidden;
  color: white;
  //background-color: skyblue;

  .article {
    height: 450px;
    border-radius: 30px;
    background-color: skyblue;

    img {
      width: 700px;
      height: 450px;

    }


  }

  .constellation {
    display: flex;
    flex-direction: row;
    border-radius: 30px;
    height: 450px;
    padding: 15px;
    background-color: yellowgreen;

    .constellationLeft {
      //width: 50%;
      text-align: left;
      //background-color: #fa243c;

      span {
        font-size: 15px;
      }

      .luck {

        ul {
          margin-top: 10px;
          list-style: none;

          li {
            margin: 3px;
            padding: 5px;
            border-radius: 8px;
            height: 25px;
            line-height: 25px;
            background-color: deeppink;
            font-size: 10px;
            display: inline-block;
          }
        }
      }

      .luckInfo {
        margin-top: 10px;

        div {
          line-height: 20px;
          height: 40px;
          font-size: 10px;
          overflow: hidden;
          text-overflow: ellipsis;

          i {
            border-radius: 5px;
            padding: 5px;
            font-style: normal;
          }
        }
      }

    }
  }

}

</style>
