<template>
  <div>
    <!--  获取QQ信息  -->
    <el-row class="axiosMain">
      <el-col :span="6" class="colorful subformat">
        <el-input class="elinput" v-model="qq" placeholder="请输入qq" size="normal" clearable></el-input>
        <el-button type="primary" size="default" @click="getQQ">查询QQ信息</el-button>
        <br>
        <div>
          <img :src="qqList.qlogo" alt="">
        </div>
        <div>
          <span>QQ账号: {{ qqList.qq }}</span>
          &nbsp;&nbsp;&nbsp;
          <span>QQ名称: {{ qqList.name }}</span>
        </div>
      </el-col>

      <!--  访问信息    -->
      <el-col :span="6" class="visitor subformat">
        <span>游客信息</span>
        <ul>
          <li>游客IP地址:&nbsp;{{ visitorList.ip }}</li>
          <li>游客系统:&nbsp;{{ visitorList.system }}</li>
          <li>游客浏览器:&nbsp;{{ visitorList.browser }}</li>
          <li>访问时间:&nbsp;{{ visitorList.time }}</li>
        </ul>
      </el-col>

      <!--   ip归属地   -->
      <el-col :span="6" class="visitor subformat">
        <span>IP归属地信息</span>
        <ul>
          <li>国家:&nbsp;{{ ipAddress.country }}</li>
          <li>省份:&nbsp;{{ ipAddress.prov }}</li>
          <li>城市:&nbsp;{{ ipAddress.city }}</li>
          <li>运营商:&nbsp;{{ ipAddress.lsp }}</li>
        </ul>
      </el-col>
      <el-col :span="6" class="translate subformat">
        <span>万能翻译</span>
        <br>
        <el-input class="elinput" v-model="translateText" placeholder="请输入要翻译的内容" size="normal"
                  clearable></el-input>
        <el-button type="primary" size="default" @click="getTranslate">点击翻译</el-button>
        <br>
        <span>翻译前:{{ this.translateText }}</span>
        <br>
        <span>翻译后:{{ this.translateBack }}</span>
      </el-col>
      <el-col :span="6" class="qrCode subformat">
        <span>生成二维码</span>
        <br>
        <el-input class="elinput" v-model="qrCodeText" placeholder="请输入要生成二维码的内容" size="normal"
                  clearable></el-input>
        <el-button type="primary" size="default" @click="getQrCode">点击生成</el-button>
        <br>
        <img :src="this.qrCodeRoute" v-if="this.qrCodeRoute">
      </el-col>
      <el-col :span="6" class="weather subformat">
        <span>天气</span>
        <br>
        <el-input class="elinput" v-model="city" placeholder="请输入要获取天气的城市" size="normal"
                  clearable></el-input>
        <el-button type="primary" size="default" @click="getWeather">点击获取</el-button>
        <div>
          <span>地点:{{ this.weatherList.city }},当前温度{{ this.weatherList.wendu }} <i
              v-if="this.weatherList.wendu">度</i></span>
        </div>
        <div style="padding: 15px;line-height: 20px">
          <span>温馨提醒: {{ this.weatherList.ganmao }}</span>
        </div>
      </el-col>
    </el-row>


  </div>
</template>

<script>
import axios from 'axios';

export default {
  // 组件加载完后触发
  mounted() {
    this.getQQ()
    this.getVisitor()
    this.getIpInfo()
    // this.getTranslate()
    this.getQrCode()
    this.getWeather()

  },
  components: {},
  data() {
    return {
      // qq
      qq: 926393691,
      qqList: [],

      // 访客
      visitor: '',
      visitorList: [],

      // ip归属地
      ipAddress: [],

      // 翻译
      translateText: '',
      translateBack: '',

      // 二维码
      qrCodeText: '二维码测试',
      qrCodeRoute: '',

      // 天气
      city: '博白',
      weatherList: [],
      yesterday: [],
      forecast: [],

    };
  },
  //方法集合
  methods: {
    getQQ() {
      axios.get('https://api.uomg.com/api/qq.info?qq=' + this.qq)
          .then((res) => {
            this.qqList = res.data
            if (this.qqList) {
              // this.$message({message: '查询QQ: ' + this.qq + ' 成功', center: 'true', type: 'success'})
              this.$notify({
                title: '成功',
                message: '查询QQ: ' + this.qq + ' 成功',
                type: 'success'
              });
            }
          })
          .catch((error) => {
            console.log(error)
          })
    },

    getVisitor() {
      axios.get('https://api.uomg.com/api/visitor.info?skey=taylor')
          .then((res) => {
            this.visitorList = res.data
            this.visitor = JSON.stringify(this.visitorList.ip)
            // this.$message({message: '欢迎游客' + this.visitor + '访问', center: 'true', type: "success",})
            // this.$notify.info({
            //   title:'成功',
            //   message:'欢迎游客' + this.visitor + '访问',
            //   type:'success'
            // });
            this.$notify({
              title: '成功',
              message: '欢迎游客'+ this.visitor +'访问',
              type: 'success'
            });
          })
          .catch((error) => {
            console.log(error)
          })

    },

    getIpInfo() {
      axios.get('https://api.vvhan.com/api/getIpInfo?ip=' + this.visitor)
          .then((res) => {
            this.ipAddress = res.data.info
          })
          .catch((error) => {
            console.log(error)
          })
    },

    getTranslate() {
      axios.get('https://api.vvhan.com/api/fy?text=' + this.translateText)
          .then((res) => {
            this.translateText = res.data.data.text
            this.translateBack = res.data.data.fanyi
            this.$notify({
              title: '成功',
              message: '翻译 '+ this.translateText +' 成功',
              type: 'success'
            });
          })
          .catch((error) => {
            console.log(error)
          })
    },

    getQrCode() {
      axios.get('https://api.vvhan.com/api/qr?text=' + this.qrCodeText)
          .then((res) => {
            this.qrCodeRoute = 'https://api.vvhan.com/api/qr?text=' + this.qrCodeText
            // this.$message({message: '生成二维码成功', center: 'true', type: "success",})
            this.$notify({
              title: '成功',
              message: '生成二维码成功',
              type: 'success'
            });
          })
          .catch((error) => {
            console.log(error)
          })
    },

    getWeather() {
      axios.get('https://api.vvhan.com/api/weather?city=' + this.city + '&type=week')
          .then((res) => {
            this.weatherList = res.data.data
            this.$notify({
              title: '成功',
              message: '获取天气成功',
              type: 'success'
            });
          })
          .catch((error) => {
            console.log(error)
          })
    }
  },
}
</script>
<style lang='less' scoped>
.axiosMain {
  box-sizing: border-box;
  padding: 0 5px;
  height: 300px;
  color: white;


  .subformat {
    margin-right: 7px;
    display: inline-block;
    width: 274.25px;
    height: 300px;
    line-height: 50px;
    border-radius: 30px;
    background-image: linear-gradient(to right,
    #030d50,
    #9d1f97,
    #ff2e97,
    #ff2999,);

    &:last-child {
      margin-right: 0;
    }


  }

  .colorful {

    .elinput {
      margin-top: 10px;
      width: 200px;
    }

    span {
      color: white;
    }

    img {
      border-radius: 50px;
    }
  }

  .visitor {
    text-align: center;
    padding: 25px;

    span {
      font-size: 15px;
    }

    li {
      list-style: none;
    }


  }

  .translate {

    .elinput {
      width: 200px;
    }

    span:first-child {
      font-size: 15px;
    }
  }

  .qrCode {

    .elinput {
      width: 200px;
    }

    span:first-child {
      font-size: 15px;
    }

    img {
      width: 146px;
      height: 146px;
    }
  }

  .weather {

    .elinput {
      width: 200px;
    }

    span:first-child {
      font-size: 15px;
    }

    .weatherLsit {

      span {
        font-size: 15px !important;
      }
    }
  }
}
</style>
